@import url('https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap');



body {
  font-family: 'Open Sans Condensed', sans-serif;
  height: 96vh;
  width: 100vw;
  margin: 0;
}
.root{
  height: 100vh;
  width: 100vw;
}
.panel{
  display: flex;
}
.left_pane{
  position: relative;
  width: 50vw;
  height: 96vh;
}
.editor_container{
  position: relative;
  height: 93vh;
  margin: 10px;
}
.right_pane{
  background-color: gray;
  position: relative;
  margin-top: 45px;
  margin-right: 20px;
  width: 50vw;
  height: 85.5vh;
}
.CodeMirror {
  height: 90% !important;
  font-size: 20px;
}
.react-codemirror2.editor{
  height: 95vh;
}
.CodeMirror .cm-s-material .CodeMirror-wrap{
  margin: 20px;
}
.console{
  position: absolute;
  height: 500px;
  width: 500px;
}
.run_btn{
  position: relative;
  left: 90%;
  width: 70px;
  height: 30px;
  margin-top: 5px;
}
.title{
  display: inline-block;
  height: 200px;
  margin-left: 20px;
  font-size: 3rem;
  font-style: italic;
}